
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

const FastClick = require('fastclick')
FastClick.attach(document.body);
export default {
name:'goodsInService',
components:{Flexbox, FlexboxItem},
props:['goodsArr','isBuy'],
methods:{
goTo(item){
if(!item.id){return ;}
let temp = this.isBuy?this.isBuy:'';
window.location.href = '/m/equipmentIndex.html?goodId='+item.id + '&isBuy='+ temp +'&=_' +(new Date().getTime());
}
}
}



