

import buyAction from '../../components/BuyAction.vue';
import goodsInService from '../../components/GoodsInService.vue';
import tools from '../../lib/tools.js';
import Toast from 'vux/src/components/toast/index.vue'

export default {
name:'serviceIndex',
components:{buyAction,goodsInService,Toast},
data(){
return {
service:{},
id:'',
info:{},
toastMsg:'',
showToast:false,
USERID:''
}
},
created(){
this.loading();
this.wxConfig();
},
methods:{
loading(){
var vm = this;
let id = tools.getRequestParam('serviceId');//此处的md为服务id

this.id = id;
let intrlligentUrl = sessionStorage.getItem('intrlligentUrl');
let url = '/service/queryGoodsById?id='+id;
if(intrlligentUrl){
url = intrlligentUrl;
sessionStorage.removeItem('intrlligentUrl');
}
this.$http.post(url,{}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
if(data.data){
vm.info = data.data[0];
vm.USERID = (localStorage.getItem('md') || tools.getRequestParam('managerId') || tools.getCookie('user_id')); // md===userid
}
}else{
vm.showToast = true;
vm.toastMsg = '查询失败';
console.log(data);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '查询失败';
});
},
wxConfig(){
let _this = this;
let config = {
jsList:['onMenuShareTimeline','onMenuShareAppMessage'],
url:location.href.split('#')[0]
}
let url = '';
let id = localStorage.getItem('md') || tools.getCookie('user_id');//当前登录人
url = id?'&sourceUserId='+id:'';
tools.wxConfig(this.$http,config,(result) => {
wx.ready(function() {
let shareData = {
title: _this.info.name,
desc: _this.info.description,
link: result.link+ 'm/sharing.html' + '?type=1&id='+_this.id+url,
imgUrl: result.link+'m/static/img/20170817165354.jpg',
};
console.log('shareData==',shareData);
wx.onMenuShareAppMessage(shareData);
wx.onMenuShareTimeline(shareData);
});
});
}
}
}



